import React from "react";
import { Box, Chip, Typography } from "@mui/material";
import cover from "../../static/img/cover.jpg"
import { info_album } from "../../static/info"
import ListenLinks from "../UI/ListenLinks";
import AlbumTracks from "../UI/AlbumTracks";
import { useNavigate } from 'react-router-dom';
import DrawerIsland from "../UI/DrawerIsland";

const AlbumPage = () => {
    // eslint-disable-next-line
    const history = useNavigate();
    document.title = `${info_album.title} - Слушать на всех площадках`
    return (
        <Box>
            <Box component="main">
                <Box>
                <Box className="flex-row gap-0 jcsb paper wrap" sx={{ mb: 1 }}>
                        <Box className="flex-row gap-0 wrap">
                           <Chip 
                                label="Новинка" 
                                variant="outlined" 
                                className="chipNew"
                                sx={{ opacity: .9 }}
                            />
                            <Chip 
                                label={info_album.track_type}
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            /> 
                            <Chip 
                                label={info_album.year_release}
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            />
                            <Chip 
                                label="№2 в чарте"
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            />
                        </Box>
                        <DrawerIsland />
                    </Box>
                    {/* <Box className="flex-row gap-1">
                        <Typography
                            variant="h6"
                            sx={{ mb: -1 }}
                            className="note2"
                        >
                            {info_album.track_type}&nbsp;&middot;&nbsp;{info_album.year_release}
                        </Typography>
                    </Box> */}
                    <Box className="flex-row wrap">
                        <Typography
                            variant="h2"
                            sx={{ ml: -0.5 }}
                        >
                            {info_album.title}&nbsp;
                        </Typography>
                        <Typography 
                            variant="overline" 
                            sx={{ fontSize: 14, mt: -1 }}
                            className="prod"
                        >
                            {info_album.prod}
                        </Typography>
                    </Box>
                    <Box className="flex-row">
                        <Typography
                            variant="h4"
                            className="note"
                        >
                            {info_album.singer}&nbsp;
                        </Typography>
                    </Box>
                </Box> 
                <Box 
                    sx={{ mt: 3 }}
                    className="cover-box"
                >
                    <img 
                        alt={`Обложка ${info_album.tracks}, исполнителя ${info_album.singer}`}
                        title={`${info_album.title} - ${info_album.singer}`}
                        src={cover}
                        className="cover"
                    />
                </Box>
            </Box>
            <AlbumTracks />
            <ListenLinks />
        </Box>
    )
}

export default AlbumPage;