import React from "react";
import { Box, Typography } from "@mui/material";

const FooterOnPage = () => {
    return (
        <Box component="footer">
            <Box
                sx={{ my: 3 }}
                className="flex-column"
            >
                <Box className="flex-row note2">
                    <Typography variant="subtitle2">
                        created by&nbsp;
                        <a href="https://github.com/roso1nik" target="_blank" rel="noopener noreferrer">
                            roso1nik
                        </a>
                        &nbsp;// v2.0
                    </Typography>
                </Box>
                <Typography variant="subtitle2" className="note2">
                    все права принадлежат их правообладателям
                </Typography>
            </Box> 
        </Box>
    )
}

export default FooterOnPage;