import React from "react";
import { Box, List, ListItem, ListItemButton, ListItemText, Tooltip, Typography } from "@mui/material";
import { info_album } from "../../static/info"
import { STARTPAGE_ROUTE } from "../../routes/consts";
import { useNavigate } from "react-router-dom"

const AlbumTracks = () => {
    const history = useNavigate();
    return (
        <Box sx={{ mb: 1 }}>
            <Typography variant="h4">
                Треки
            </Typography>
            <Box
                sx={{ my: 3 }}
                className="flex-row wrap gap-1 listen-box"
            >
                <List className="wfull">
                    {info_album.tracks.map(track => (
                        <ListItem 
                            onClick={() => history(STARTPAGE_ROUTE)}
                            key={Math.random()}
                        >
                            <ListItemText 
                                primary={track}
                            />
                            <Tooltip 
                                title={`Слушать "${track}"\n на всех площадках`} 
                                placement="right" 
                                arrow
                            >
                                <ListItemButton 
                                    sx={{ display: "flex", justifyContent: "flex-end"}} 
                                    onClick={() => history(STARTPAGE_ROUTE)}
                                    className="note"
                                >
                                    слушать
                                </ListItemButton>
                            </Tooltip>
                        </ListItem>
                    ))}
                </List> 
            </Box> 
        </Box>
    )
}

export default AlbumTracks;