import React from "react";
import { Box, Typography, Button, Chip } from "@mui/material";
import cover from "../../static/img/cover.jpg";
import { info } from "../../static/info";
import ListenLinks from "../UI/ListenLinks";
import { ALBUM_ROUTE } from "../../routes/consts";
import { useNavigate } from 'react-router-dom';
import DrawerIsland from "../UI/DrawerIsland";


const Start = () => {
    const history = useNavigate();
    document.title = `${info.track} - Слушать на всех площадках`
    return (
        <Box>
            <Box component="main">
                <Box>
                    <Box className="flex-row gap-0 jcsb paper wrap" sx={{ mb: 1 }}>
                        <Box className="flex-row gap-0 wrap">
                           <Chip 
                                label="Новинка" 
                                variant="outlined" 
                                className="chipNew"
                                sx={{ opacity: .9 }}
                            />
                            <Chip 
                                label={info.track_type}
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            /> 
                            <Chip 
                                label={info.year_release}
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            />
                            <Chip 
                                label="№4 в чарте"
                                variant="outlined" 
                                className="chipType"
                                sx={{ opacity: .9 }}
                            />
                        </Box>
                        <DrawerIsland />
                    </Box>
                    {/* <Box className="flex-row gap-1">
                        <Typography
                            variant="h6"
                            sx={{ mb: -1 }}
                            className="note2"
                        >
                            {info.track_type}&nbsp;&middot;&nbsp;{info.year_release}
                        </Typography>
                    </Box> */}
                    <Box className="flex-row wrap">
                        <Typography
                            variant="h2"
                        >
                            {info.track}&nbsp;
                        </Typography>
                        <Typography 
                            variant="overline" 
                            sx={{ fontSize: 14 }}
                            className="prod"
                        >
                            {info.prod}
                        </Typography>
                    </Box>
                    <Box className="flex-row">
                        <Typography
                            variant="h4"
                            className="note"
                        >
                            {info.singer}&nbsp;
                        </Typography>
                    </Box>
                </Box> 
                <Box 
                    sx={{ mt: 3 }}
                    className="cover-box"
                >
                    <img 
                        alt={`Обложка трека ${info.track}, исполнителя ${info.singer}`}
                        title={`${info.track} - ${info.singer}`}
                        src={cover}
                        className="cover"
                    />
                    <Box className="note2 wfull jcc">
                        <Button 
                            className="buttonGoToAlbum note2"
                            variant="link" 
                            onClick={() => history(ALBUM_ROUTE)}
                            // sx={{ ml: 0.5, mb: -1, px: 0 }}
                        >
                            перейти к альбому
                        </Button>
                    </Box>
                </Box>
            </Box>
            <ListenLinks />
        </Box>
    )
}

export default Start;