import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import { IconButton, Tooltip, Typography } from '@mui/material';
import ShareIcon from '@mui/icons-material/Share';
import { info, info_album } from "../../static/info";
import cover from "../../static/img/cover.jpg";
import ListenLinks from './ListenLinks';

export default function DrawerIsland() {

  const path = window.location.pathname;

  const [state, setState] = React.useState({
    top: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
      className="DrawerIsland"
    >
        <Box className="flex-column">
            <Box className="flex-row gap-1 island_f">
                <img 
                    alt={`Обложка трека ${info.track}, исполнителя ${info.singer}`}
                    title={`${info.track} - ${info.singer}`}
                    src={cover}
                    className="cover cover_f"
                /> 
                <Box className="flex-column acc">
                    <Box className="flex-column acc islandInfo">
                        {path === '/album'
                            ?   <>
                                    <Typography variant="h2">
                                        {info_album.title}
                                    </Typography>
                                    <Typography variant="h2" className="note">
                                        {info_album.singer}
                                    </Typography> 
                                </>
                            :   <>
                                   <Typography variant="h2">
                                        {info.track}
                                    </Typography>
                                    <Typography variant="h2" className="note">
                                        {info.singer}
                                    </Typography> 
                                </>
                        }
                    </Box>
                    <Box className="islandhrefs">
                        <ListenLinks type="island" />
                    </Box>
                </Box>
                <Box className="islandhrefs_f">
                    <ListenLinks type="island" />
                </Box>
            </Box>
        </Box>
    </Box>
  );

  return (
    <div>
      {['top'].map((anchor) => (
        <React.Fragment key={anchor}>
            <Tooltip title="Поделиться" arrow>
                <IconButton onClick={toggleDrawer(anchor, true)}>
                    <ShareIcon className="note" sx={{ opacity: .9 }}/>
                </IconButton>
            </Tooltip>
          <Drawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
          >
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}